.toast {
  position: fixed;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  top: 50%;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  text-align: center;
  padding: 5px;
  -webkit-transition: all .3s;
  transition: all .3s;
}
.hideStart {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

.ui {
  /*border: 1px solid red;*/
  background: blue;
}
.ui-check {
  /*border: 1px solid red;*/
  background: green;
}
.ui-none {
  /*border: 1px solid red;*/
  background: red;
}
.m-checkbox {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  cursor: pointer;
}
.m-checkbox input {
    opacity: 0;
    visibility: hidden;
    vertical-align: middle;
}
.m-checkbox .input-radio {
    display: inline-block;
    vertical-align: middle;
}
.m-checkbox .input-radio {
    padding-left: 10px;
}

.input {
  display: inline-block;
  vertical-align: middle;
  width: 200px;
}
.input input {
    color: #606266;
    padding: 0 15px;
    border-radius: 4px;
    border: 1px solid #ddd;
    width: 100%;
    height: 38px;
    -webkit-transition: all .2s;
    transition: all .2s;
}
.input input:focus {
      outline: none;
      border: 1px solid #409eff;
}

.m-radio {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  cursor: pointer;
}
.m-radio .input-radio, .m-radio .input-radio-ui {
    display: inline-block;
    vertical-align: middle;
}
.m-radio .input-radio {
    padding-left: 10px;
}
.m-radio .input-radio-ui {
    white-space: nowrap;
    line-height: 1;
    position: relative;
    width: 14px;
    height: 12px;
}
.m-radio .input-radio-ui .radio-inner {
      display: inline-block;
      width: 14px;
      height: 14px;
      border: 1px solid #dcdfe6;
      border-radius: 50%;
      position: relative;
      border: 1px solid #dcdfe6;
      -webkit-transition: all .3s;
      transition: all .3s;
}
.m-radio .input-radio-ui .radio-inner:after {
        content: " ";
        display: inline-block;
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%) scale(0);
                transform: translate(-50%, -50%) scale(0);
        background: #fff;
        width: 4px;
        height: 4px;
        border-radius: 50%;
        -webkit-transition: all .3s;
        transition: all .3s;
}
.input-radio-check .radio-inner {
  background: #409eff;
  border: none;
}
.input-radio-check .radio-inner:after {
    -webkit-transform: translate(-50%, -50%) scale(1) !important;
            transform: translate(-50%, -50%) scale(1) !important;
}
.input-radio-check .input-radio {
  color: #409eff;
}
.input-radio-disabled-check .radio-inner {
  background: #e4e7ed;
  border: none;
}
.input-radio-disabled-check .radio-inner:after {
    background: #c0c4cc;
    -webkit-transform: translate(-50%, -50%) scale(1) !important;
            transform: translate(-50%, -50%) scale(1) !important;
}
.input-radio-disabled-check .input-radio {
  color: #e4e7ed;
}
.input-radio-disabled .radio-inner {
  background: #e4e7ed;
  border: none;
}
.input-radio-disabled .input-radio {
  color: #e4e7ed;
}

.switch-box {
  display: inline-block;
  vertical-align: middle;
}
.switch {
  width: 40px;
  height: 20px;
  border-radius: 10px;
  display: inline-block;
  vertical-align: middle;
  background: #dcdfe6;
  position: relative;
  -webkit-transition: all .3s;
  transition: all .3s;
}
.switch input {
    position: absolute;
    width: 0;
    height: 0;
    opacity: 0;
}
.switch span {
    cursor: pointer;
    position: absolute;
    left: 2px;
    top: 2px;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: #fff;
    display: inline-block;
    -webkit-transition: all .3s;
    transition: all .3s;
}
.change-move {
  background: #409eff;
}
.change-move span {
    -webkit-transform: translate3d(20px, 0, 0);
            transform: translate3d(20px, 0, 0);
}
